import { SET_DEVICE_REGISTRATION_INFO } from "../actions/action-types";


const initialState = {
    deviceRegistrationData: ""
};

export const deviceRegistrationReducer = (state = initialState, action) => {
    switch (action.type) {
        case SET_DEVICE_REGISTRATION_INFO:
            return {
                ...state,
                deviceRegistrationData: action.payload
            };
        default:
            return state;
    }
};
