import { SET_RESPONSE_ERROR, SET_USER } from "../actions/action-types";

export const userReducer = (state = {}, action) => {
    switch (action.type) {
        case SET_USER:
            return {
                ...state,
                user: action.payload
            };

        case SET_RESPONSE_ERROR:
            return {
                ...state,
                error: action.payload
            }
        default:
            return state;
    }
};
